import React, { useEffect, useState } from 'react';
import Web3 from 'web3';
import './App.css';

// Replace with your contract ABI and address
const contractAddress = '0x74E963Bf58cFdDe89fDA52f21025FA406dDcC3b7';
const abi = [
    {
        "inputs": [],
        "stateMutability": "nonpayable",
        "type": "constructor"
    },
    {
        "inputs": [
            {
                "internalType": "string",
                "name": "name",
                "type": "string"
            },
            {
                "internalType": "string",
                "name": "message",
                "type": "string"
            }
        ],
        "name": "buyChai",
        "outputs": [],
        "stateMutability": "payable",
        "type": "function"
    },
    {
        "inputs": [],
        "name": "getMemos",
        "outputs": [
            {
                "components": [
                    {
                        "internalType": "string",
                        "name": "name",
                        "type": "string"
                    },
                    {
                        "internalType": "string",
                        "name": "message",
                        "type": "string"
                    },
                    {
                        "internalType": "uint256",
                        "name": "timestamp",
                        "type": "uint256"
                    },
                    {
                        "internalType": "address",
                        "name": "from",
                        "type": "address"
                    }
                ],
                "internalType": "struct Chai.Memo[]",
                "name": "",
                "type": "tuple[]"
            }
        ],
        "stateMutability": "view",
        "type": "function"
    },
    {
        "inputs": [
            {
                "internalType": "uint256",
                "name": "",
                "type": "uint256"
            }
        ],
        "name": "memos",
        "outputs": [
            {
                "internalType": "string",
                "name": "name",
                "type": "string"
            },
            {
                "internalType": "string",
                "name": "message",
                "type": "string"
            },
            {
                "internalType": "uint256",
                "name": "timestamp",
                "type": "uint256"
            },
            {
                "internalType": "address",
                "name": "from",
                "type": "address"
            }
        ],
        "stateMutability": "view",
        "type": "function"
    }
];

const App = () => {
    const [account, setAccount] = useState('');
    const [contract, setContract] = useState(null);
    const [name, setName] = useState('');
    const [message, setMessage] = useState('');
    const [memos, setMemos] = useState([]);
    const [amount, setAmount] = useState('');
    useEffect(() => {
        const loadWeb3 = async () => {
            if (window.ethereum) {
                const web3 = new Web3(window.ethereum);
                await window.ethereum.request({ method: 'eth_requestAccounts' });
                const accounts = await web3.eth.getAccounts();
                setAccount(accounts[0]);

                const contractInstance = new web3.eth.Contract(abi, contractAddress);
                setContract(contractInstance);
            } else {
                alert('Please install MetaMask!');
            }
        };

        loadWeb3();
    }, []);

    const buyChai = async () => {
        if (contract && account) {
            try {
                const value = Web3.utils.toWei(amount, 'ether');// Adjust the amount as needed
                await contract.methods.buyChai(name, message).send({ from: account, value });
                alert('Chai purchased successfully!');
                setName('');
                setMessage('');
                fetchMemos(); // Refresh the memos after buying chai
            } catch (error) {
                console.error('Error buying chai:', error);
            }
        }
    };

    const fetchMemos = async () => {
        if (contract) {
            try {
                const result = await contract.methods.getMemos().call();
                setMemos(result);
            } catch (error) {
                console.error('Error fetching memos:', error);
            }
        }
    };

    return (
        <div formalign>
        
            <h1 className='heading'>Chai Smart Contract DApp</h1>
            <h2>Account: {account}</h2>
            
            <h2 className='subheading'>Buy me a Chai</h2>
            <div>
            <input className='form'
                type="text"
                value={name}
                onChange={(e) => setName(e.target.value)}
                placeholder="Your Name"
            />
            <input className='form'
                type="text"
                value={message}
                onChange={(e) => setMessage(e.target.value)}
                placeholder="Your Message"
            />
            <input className='form'
                type="text"
                value={amount}
                onChange={(e) => setAmount(e.target.value)}
                placeholder="Amount in Ether"
            />
            <div className='btndiv'>
            <button className='btn' onClick={buyChai}>Buy Chai</button>
            </div>
            </div>
            <h2 className='getmemo'>Get Memos</h2>
            <button className='fetchmemo' onClick={fetchMemos}>Fetch Memos</button>
            <div>
                {memos.length > 0 ? (
                    <ul className='list'>
                        {memos.map((memo, index) => (
                            <li key={index}>
                                <strong>{memo.name}</strong>: {memo.message} (From: {memo.from}, Time: {new Date(memo.timestamp * 1000).toString()})
                            </li>
                        ))}
                    </ul>
                ) : (
                    <p>No memos found.</p>
                )}
            </div>
        </div>
    );
};

export default App;
